import React, { useEffect, useState } from 'react';
import axios from 'axios';
import ReactStars from 'react-stars';


interface Rating {
  _id: string;
  email: string;
  stars: number;
  message: string;
  date: string;
}

const Testimonials: React.FC = () => {
  const [ratings, setRatings] = useState<Rating[]>([]);
  const [loading, setLoading] = useState<boolean>(true);
  const [error, setError] = useState<string>('');

  useEffect(() => {
    const fetchRatings = async () => {
      try {
        const response = await axios.get('/api/testimonials');
        setRatings(response.data);
      } catch (err) {
        setError('Error fetching ratings');
      } finally {
        setLoading(false);
      }
    };

    fetchRatings();
  }, []);

  if (loading) {
    return <p className="text-center text-gray-600">Loading...</p>;
  }
  if (error) return <p style={{ color: 'red' }}>{error}</p>;

  return (
    <div className="max-w-4xl mx-auto p-6">
     <h2 className="text-2xl font-semibold mb-4 text-center">
        Notes et t√©moignages
      </h2>


      <section>
        <br />
        <div className="space-y-4">
          {ratings.length ? (
            ratings.map((rating) => (
              <div key={rating._id} className="p-4 border border-gray-300 rounded-md">
                <div className="flex items-center mb-2">
                  <ReactStars
                    count={5}
                    size={24}
                    color2={'#ffd700'}
                    value={rating.stars}
                    edit={false}
                  />
              
                </div>
                <span className=" text-gray-700"><strong> {rating.email}</strong></span>
                <p className="mb-2">{rating.message}</p>
                <p className="text-sm text-gray-500">Posted on {new Date(rating.date).toLocaleDateString()}</p>
              </div>
            ))
          ) : (
            <p>Pas de Notes disponibles.</p>
          )}
        </div>
      </section>
    </div>
  );
};

export default Testimonials;
